export default {
	data(){
		return {
			system:this.systemInfo(),
			goodsImages:{},
			goodsImgHeightInit:300,
			detaillist:[],
			editIndex:-1
		}
	},
	onLoad(option) {
		let detailJson = option.detail;
		if(detailJson){
			this.detaillist = JSON.parse(detailJson);
		}
	},
	methods:{
		adddetail(e){
			let index = e.currentTarget.dataset.index;
			
			uni.showActionSheet({
				itemList: ['图片', '文本'],
				success: (res) => {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					
					if(res.tapIndex == 0){
						uni.chooseImage({
							count: 1, //默认9
							sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
							sourceType: ['album'], //从相册选择
							success: (res) => {
								console.log(JSON.stringify(res.tempFilePaths));
								
								uni.showLoading({
									title:"加载中"
								});
								this.loading = true;
								uni.uploadFile({
									url: 'https://www.dpsoft.top/upload.jsp', 
									filePath: res.tempFilePaths[0],
									name: 'file',
									
									success: (uploadFileRes) => {
										console.log(uploadFileRes.data);
										let data = JSON.parse(uploadFileRes.data);
										if("ok" == data.status){
											let webpathSuffix = data.data;
											let url = "http://www.dpsoft.top/"+webpathSuffix;
											let detail = {};
											detail.type = 'image';
											detail.value = url;
											if(index != null){
												this.detaillist.splice(index,0,detail);
											}else{
												this.detaillist.push(detail);
											}
										}
										
									},
									complete: ()=> {
										uni.hideLoading();
									}
								});
								
							}
						});
					}
					if(res.tapIndex == 1){
						let detail = {};
						detail.type = 'text';
						detail.value = '';
						if(index != null){
							this.detaillist.splice(index,0,detail);
						}else{
							this.detaillist.push(detail);
						}
					}
					
				},
				fail: (res) => {
					console.log(res.errMsg);
				}
			});
		},
		editText(e){
			let index = e.currentTarget.dataset.index;
			if(this.editIndex != index){
				uni.showActionSheet({
					itemList: ['编辑', '删除'],
					success: (res) => {
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						if(0 == res.tapIndex){
							this.editIndex = index;
						}
						if(1 == res.tapIndex){
							this.detaillist.splice(index, 1);
						}
					}
				});
			}
			
			
			
		},
		editFinish(){
			this.editIndex = -1;
		},
		editImage(e){
			let index = e.currentTarget.dataset.index;
			uni.showActionSheet({
				itemList: ['删除'],
				success: (res) => {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					
					if(0 == res.tapIndex){
						this.detaillist.splice(index, 1);
					}
				}
			});
		},
		goodsImageLoad:function(e){
			
			let imgId = e.target.id;
			let height = e.target.height;
			let width = e.target.width;
			let imgWidth = (750-60)*this.system.windowWidth/750;
			let imgHeight = height * imgWidth / width;
			
			this.goodsImages[imgId] = {
				width:imgWidth,
				height:imgHeight
			};
			this.goodsImgHeightInit = imgHeight;
			
			
		},
		submit(){
			if(this.detaillist.length<=0){
				uni.showToast({
					icon:'none',
					title:'您还没有添加详情'
				});
				return;
			}
			uni.setStorageSync("detail", JSON.stringify(this.detaillist));
			uni.navigateBack({
				
			});
		}
	}
}

